import "./ooperation.css";
import iconDiscount from "../../images/icon-discount.svg";
import iconDocument from "../../images/icon-document.svg";
import iconWarehouse from "../../images/icon-warehouse.svg";
import iconGuarantee from "../../images/icon-guarantee.svg";
import iconPayment from "../../images/icon-payment.svg";
import iconDelivery from "../../images/icon-delivery.svg";
import Button from "../Button/Button";
import arrow from "../../images/icon-arrow.svg";
import * as Yup from "yup";
import { Formik, Field, Form, ErrorMessage } from "formik";
import React, { useState, useEffect } from "react";

function ooperation() {
  const validationSchema = Yup.object({
    name: Yup.string()
      .required("  ")
      .min(2, "     2 "),
    phone: Yup.string()
      .required("  ")
      .matches(/^\+7\d{10}$/, "     +7XXXXXXXXXX"),
    email: Yup.string()
      .email("  email")
      .required(" email"),
  });

  const [buttonText, setButtonText] = useState(
    "   "
  );

  useEffect(() => {
    const updateButtonText = () => {
      if (window.innerWidth < 768) {
        setButtonText(" ");
      } else {
        setButtonText("   ");
      }
    };

    //   
    updateButtonText();

    //      
    window.addEventListener("resize", updateButtonText);

    //     
    return () => {
      window.removeEventListener("resize", updateButtonText);
    };
  }, []);

  return (
    <div className="cooperation">
      <div className="container">
        <div className="cooperation__c">
          <div className="cooperation__c-top">
            <p className="txt-sans60">   </p>
            <p className="txt-sans32">
                 ,   
               ,      
              .
            </p>
          </div>
          <div className="cooperation__c-center">
            <p className="txt-sans48">  :</p>
            <ul className="cooperation__list">
              <li className="cooperation__item">
                <div className="cooperation__item-top">
                  <img alt="" src={iconDiscount} />
                  <p className="txt-sans32">   </p>
                </div>
                <p className="txt-sans24">
                          
                  ,       
                      .
                </p>
              </li>
              <li className="cooperation__item">
                <div className="cooperation__item-top">
                  <img alt="" src={iconDocument} />
                  <p className="txt-sans32"> </p>
                </div>
                <p className="txt-sans24">
                       , 
                        
                  ,     .
                </p>
              </li>
              <li className="cooperation__item">
                <div className="cooperation__item-top">
                  <img alt="" src={iconWarehouse} />
                  <p className="txt-sans32">  </p>
                </div>
                <p className="txt-sans24">
                       
                      .    
                      .
                </p>
              </li>
              <li className="cooperation__item">
                <div className="cooperation__item-top">
                  <img alt="" src={iconGuarantee} />
                  <p className="txt-sans32">  </p>
                </div>
                <p className="txt-sans24">
                        ,  
                        
                    .
                </p>
              </li>
              <li className="cooperation__item">
                <div className="cooperation__item-top">
                  <img alt="" src={iconPayment} />
                  <p className="txt-sans32">  </p>
                </div>
                <p className="txt-sans24">
                         
                    ,       
                     .
                </p>
              </li>
              <li className="cooperation__item">
                <div className="cooperation__item-top">
                  <img alt="" src={iconDelivery} />
                  <p className="txt-sans32">    </p>
                </div>
                <p className="txt-sans24">
                          
                    ,     
                    .
                </p>
              </li>
            </ul>
          </div>
          <div className="cooperation__c-btm">
            <Formik
              initialValues={{ name: "", phone: "", email: "" }}
              validationSchema={validationSchema}
              onSubmit={(values, { resetForm }) => {
                console.log(values);
                resetForm();
              }}
            >
              {({ isSubmitting }) => (
                <Form className="cooperation__form">
                  <p className="txt-sans48">   </p>
                  <div className="cooperation__form-box">
                    <div className="cooperation__form-box">
                      <div className="cooperation__label">
                        <Field
                          className="cooperation__input txt-sans24"
                          placeholder=" "
                          name="name"
                          type="text"
                        />
                        <ErrorMessage
                          name="name"
                          component="span"
                          className="error-message txt-sans14"
                        />
                      </div>
                      <div className="cooperation__label">
                        <Field
                          className="cooperation__input txt-sans24"
                          placeholder=" "
                          type="tel"
                          name="phone"
                        />
                        <ErrorMessage
                          name="phone"
                          component="span"
                          className="error-message txt-sans14"
                        />
                      </div>
                      <div className="cooperation__label">
                        <Field
                          className="cooperation__input txt-sans24"
                          placeholder=" E-mail"
                          type="email"
                          name="email"
                        />
                        <ErrorMessage
                          name="email"
                          component="span"
                          className="error-message txt-sans14"
                        />
                      </div>
                    </div>
                    <p className="txt-sans20">
                       ,     
                        .
                    </p>
                  </div>
                  <div className="cooperation__btn">
                    <Button
                      src={arrow}
                      text={buttonText}
                      type="submit"
                      disabled={isSubmitting}
                    />
                  </div>
                </Form>
              )}
            </Formik>
          </div>
        </div>
      </div>
    </div>
  );
}

export default ooperation;
